
'use client'
import Image from 'next/image'
import { useEffect, useState } from 'react'

type Slide = { src: string; title: string; subtitle?: string }

const slides: Slide[] = [
  { src: 'https://images.unsplash.com/photo-1498050108023-c5249f4df085?q=80&w=1600&auto=format&fit=crop', title: 'Welcome to Our Company', subtitle: 'Delivering IT solutions that move you forward' },
  { src: 'https://images.unsplash.com/photo-1521737604893-d14cc237f11d?q=80&w=1600&auto=format&fit=crop', title: 'We Build & Support', subtitle: 'From concept to scale' },
  { src: 'https://images.unsplash.com/photo-1519389950473-47ba0277781c?q=80&w=1600&auto=format&fit=crop', title: 'Your Success, Our Mission', subtitle: 'Reliable, secure, and efficient' }
]

export default function HeroSlider() {
  const [index, setIndex] = useState(0)

  useEffect(() => {
    const id = setInterval(() => setIndex(i => (i + 1) % slides.length), 5000)
    return () => clearInterval(id)
  }, [])

  const prev = () => setIndex(i => (i - 1 + slides.length) % slides.length)
  const next = () => setIndex(i => (i + 1) % slides.length)

  return (
    <div className="relative h-[80vh] w-full overflow-hidden">
      {slides.map((s, i) => (
        <div key={s.src} className={`absolute inset-0 transition-opacity duration-700 ${i===index ? 'opacity-100' : 'opacity-0'}`} aria-hidden={i!==index}>
          <Image src={s.src} alt={s.title} fill priority className="object-cover" />
          <div className="absolute inset-0 bg-black/40" />
          <div className="absolute inset-0 container-px flex flex-col items-start justify-center text-white">
            <h1 className="text-3xl sm:text-5xl font-bold max-w-3xl">{s.title}</h1>
            {s.subtitle && <p className="mt-4 text-base sm:text-lg max-w-2xl text-white/90">{s.subtitle}</p>}
            <a href="#services" className="mt-8 inline-flex items-center px-5 py-3 bg-brand-500 hover:bg-brand-600 text-white rounded-md">Explore Services</a>
          </div>
        </div>
      ))}
      <div className="absolute left-0 right-0 bottom-6 flex items-center justify-center gap-2">
        {slides.map((_, i) => (
          <button key={i} onClick={() => setIndex(i)} className={`h-2.5 w-2.5 rounded-full ${i===index ? 'bg-white' : 'bg-white/50'}`} aria-label={`Go to slide ${i+1}`}></button>
        ))}
      </div>
      <div className="absolute inset-y-0 left-0 right-0 pointer-events-none">
        <div className="h-full flex justify-between items-center">
          <button onClick={prev} className="pointer-events-auto ml-2 sm:ml-4 p-2 rounded-full bg-black/30 text-white hover:bg-black/50" aria-label="Previous slide">‹</button>
          <button onClick={next} className="pointer-events-auto mr-2 sm:mr-4 p-2 rounded-full bg-black/30 text-white hover:bg-black/50" aria-label="Next slide">›</button>
        </div>
      </div>
    </div>
  )
}
